@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
.Navbar {
    background-color: var(--background-p);
    position: fixed;
    opacity: 0.98;
    width: 100%;
    height: 64px;
}

.NavContent {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: 64px;
    padding-left: 7%;
    padding-right: 7%;
    justify-content: space-between;
    align-items: center;
}

.NavContent a {
    text-decoration: none;
}

.ProjectName p {
    color: var(--text-light-p);
    font-size: 1.4rem;
    margin: 0;
}

.Homepage {
    background-color: var(--background-p);
    font-family: var(--font);
    color: var(--text-light-p);
    font-size: 1rem;
    width: 100%;
    height: 100vh;
}

.MainBody {
    padding-top: 64px;
}

.ProjectInfo {
    text-align: center;
}

.ProjectInfo h2 {
    font-size: 4.75rem;
    line-height: 75px;
    font-weight: 600;
    margin-top: 7%;
}

.ProjectInfo h4 {
    color: var(--text-light-s);
    font-size: 1.3rem;
    line-height: 30px;
    margin-top: 2%;
}

.Login {
    background-color: var(--background-p);
    font-family: var(--font);
    color: var(--text-light-p);
    font-size: 1rem;
    width: 100%;
    height: 100vh;
}

.Card {
    position: absolute;
    background-color: #3b3b3b;
    border-radius: 20px;
    padding-left: 40px;
    padding-right: 40px;
    width: 500px;
    height: 600px;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.CardContent {
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

.CardLabel {
    margin-top: 20px !important;
    margin-bottom: 0px !important;
}

.SubmitButton {
    margin-top: 60px !important;
    width: 100%;
    height: 40px;
}

.LoginLink {
    margin-top: 10px;
}

