/* DEFAULT MARGIN AND PADDING */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* ROOT VARIABLES FOR STYLE */
:root {
    --accent: #c38fff;
    --background-p: #202020;
    --background-s: #000000;
    --text-light-p: #f2f2f2;
    --text-light-s: #8f8f8f;
    --font: 'Poppins', sans-serif;
}

/* DEFAULT SCROLLBAR */
::-webkit-scrollbar {
    width: 10px;
}
::-webkit-scrollbar-thumb {
    background: #c38fff;
    background: var(--accent);
}
::-webkit-scrollbar-track {
    background: #202020;
    background: var(--background-p);
}

/* DEFAULT STYLING */
html {
    background-color: #202020;
    background-color: var(--background-p);
    font-family: 'Poppins', sans-serif;
    font-family: var(--font);
    color: #f2f2f2;
    color: var(--text-light-p);
}

